
import { reactive, ref } from 'vue'

interface Todo {
  id: string;
  title: string;
}

export default {
  name: 'TodoList',
  setup () {
    // ref:
    // 可以接受「任何型態」的資料
    // 但是不會對「物件」或「陣列」內部的屬性變動做監聽

    // reactive
    // 「只接受」物件或陣列，可以做深層的監聽
    // 以及取得資料不需要 「.value」
    const newTodo = ref('')
    const todoList = reactive<Todo[]>([])

    // methods
    function addTodo () {
      if (newTodo.value) {
        todoList.push({
          id: Math.floor(Date.now()).toString(),
          title: newTodo.value
        })
        newTodo.value = ''
      }
    }

    function removeTodo (idx: number) {
      todoList.splice(idx, 1)
    }

    // return
    return {
      todoList,
      newTodo,
      addTodo,
      removeTodo
    }
  }
}
